import React, { useState } from 'react';
import './Table.sass'

export function Table(props) {
  const user = props.user
  const cards = props.cards
  const turn = props.turn
  const players = props.players

  const cardList = players.map((p) => {
    const c = p.card_played
    const backgroundFile = "cards/" + c + ".png"
    const relPos = ((-user.position + 4) + p.position) % 4

    return (
      <img className={`table-card p${relPos} ${c >= 0 ? "played" : ""}`} alt="table-card" key={c.toString()} src={backgroundFile} />
    )
  })

  return (
    <div className="table-container">
      <div className="table">
        {cardList}
      </div>
    </div>
  )
}
