import React, { useState } from 'react';
import {socket} from '../../socket'
import './Player.sass'

export function Player(props) {
  const game = props.game
  const user = props.user
  const player = props.player
  const turn = props.turn

  // position relative to user
  const bearing = ((-user.position + 4) + player.position) % 4
  const bearingName = `p${bearing}`

  const visible = (player.dummy) ||
                (player.position === user.position) ||
                (user.dummy && player.declarer)

  // Player turn
  const player_turn = player.position === turn

  // Can be controlled by user
  let controllable = (player.position === user.position) ||
                     (user.declarer && player.position === ((user.position + 2) % 4 ))
  controllable = controllable & player_turn
  if (user.dummy) {
    controllable = false
  }

  // cards
  const playerHand = player.cards.sort((a,b) => a - b).map((c) => {
    let backgroundFile = "cards/back2.jpg"
    if (visible) {
      backgroundFile = "cards/" + c + ".png"
    }
    if (controllable && player_turn) {
      return (
        <img className={`player-card ${visible ? "visible" : ""}`} alt="card" key={c.toString()} src={backgroundFile} onClick={() => playCard(game.game_key,player.key, c)}/>
      )
    } else {
      return (
        <img className={`player-card ${visible ? "visible" : ""}`} alt="card" key={c.toString()} src={backgroundFile} />
      )
    }
  })

  return (
    <div className={`player ${bearingName} ${controllable ? "controllable" : ""} ${player_turn ? "player-turn" : ""}`}>
      <PlayerHeader player={ player }/>
      <div className="player-hand">
        { playerHand }
      </div>

    </div>
  )
}

function playCard(game, player, card) {
  console.log(game);
  if (!game) {
    return
  }
  socket.emit("play_card", {
    "player_key": player,
    "game_key": game,
    "card_played": card
  });
}


function PlayerHeader(props) {
  const player = props.player
  return (
    <div className="player-header">
      <div className="player-name">
        <span className="name"> {player.name}</span>
        { player.dummy && <span className="dummy"> (Dummy)</span> }
        { player.declarer && <span className="declarer"> (Declarer)</span> }
        { player.bot && <span className="bot">BOT </span> }
      </div>
      <div>
        { player.points }
      </div>
    </div>
  )
}
